<template>
    <div class="contrast">
        <header class="header">
            <img :src="contrast.img" alt="">
        </header>
        <div class="one">
            <contrast-item
                class="item"
                v-for="(item, i) in one"
                :key="i"
                :data="item"
            />
        </div>
        <div class="two">
            <header class="my-header">
                <h2>{{ two.title }}</h2>
            </header>
            <div class="box">
                <contrast-item
                    class="item"
                    v-for="(item, i) in two.data"
                    :key="i"
                    :data="item"
                />
            </div>
        </div>
        <div class="two">
            <header class="my-header">
                <h2>{{ three.title }}</h2>
            </header>
            <div class="box">
                <contrast-item
                    class="item"
                    v-for="(item, i) in three.data"
                    :key="i"
                    :data="item"
                />
            </div>
        </div>
        <div class="two">
            <header class="my-header">
                <h2>{{ four.title }}</h2>
            </header>
            <div class="box">
                <contrast-item
                    class="item"
                    v-for="(item, i) in four.data"
                    :key="i"
                    :data="item"
                />
            </div>
        </div>
    </div>
</template>

<script>
    import ContrastItem from './ContrastItem';

    // 对比
    export default {
        name: 'contrast',
        props: {
            contrast: {
                type: Object,
                default () {
                    return {
                        img: '',
                        modules: {}
                    }
                }
            }
        },
        computed: {
            one () {
                if (this.contrast.modules.one == void 0) {
                    return [];
                }
                return this.contrast.modules.one;
            },
            two () {
                if (this.contrast.modules.two == void 0) {
                    return [];
                }
                return this.contrast.modules.two;
            },
            three () {
                if (this.contrast.modules.three == void 0) {
                    return [];
                }
                return this.contrast.modules.three;
            },
            four () {
                if (this.contrast.modules.four == void 0) {
                    return [];
                }
                return this.contrast.modules.four;
            }
        },
        components: {
            ContrastItem
        }
    }
</script>

<style lang="less" scoped>
    .contrast {
        .header {
            margin: 50px 0;
            text-align: center;
        }
        .one {
            column-count: 2;
            column-gap: 10px;
            .item {
                break-inside: avoid;
                margin-bottom: 50px;
            }
        }
        .two {
            margin-bottom: 50px;
            .my-header {
                margin-bottom: 50px;
                text-align: center;
                h2 {
                    display: inline;
                    padding: 6px 20px;
                    color: #FFF;
                    font-size: 20px;
                    font-weight: bold;
                    background-color: #cccccc;
                    border-radius: 3px;
                }
            }
            .box {
                column-count: 2;
                column-gap: 10px;
                .item {
                    break-inside: avoid;
                    margin-bottom: 50px;
                }
            }
        }
    }

    @media screen and (max-width: 1023px) {
        .contrast {
            .header {
                margin: 30px 0;
            }
            .one {
                column-count: 1;
            }
            .two {
                .box {
                    column-count: 1;
                }
            }
        }
    }
</style>